@import '../../theme.scss';
#sponsors {
	.sponsor-grid {
		display: grid;
		grid-template-columns: repeat(4, 1fr);
		grid-template-rows: repeat(2, 1fr);
		column-gap: 10%;
		row-gap: 60px;
		max-width: 1100px;
		margin: 0 auto;
		.sponsor {
			width: 100%;
			img {
				width: 100%;
			}
			@include mobile {
				padding: 0 80px;
			}
		}
		@include mobile {
			grid-template-columns: 1fr;
			column-gap: 20px;
			row-gap: 30px;
		}
	}
}