@import '../theme.scss';
body {
	padding-top: 90px;
}

header {
	position: fixed;
	width: 100vw;
	top: 0;
	left: 0;
	padding: 15px 0;
	z-index: 100;
	transition: all 0.2s ease-out;

	&>div {
		display: flex;
		align-content: center;
		align-items: center;
	}
	.brand-image {
		height: 60px;
		transition: all 0.2s ease-out;
	}
	h1 {
		font-size: 2.3rem;
		font-weight: 700;
		margin: 0;
		flex: 1;
		padding-left: 40px;
		padding-top: 5px;
		transition: all 0.2s ease-out;
	}

	&:not(.transparent) {
		background-color: $light;
		box-shadow: 0px 10px 40px rgba(51, 51, 51, 0.2);
		padding: 10px 0;
		@include desktop {
			.brand-image {
				height: 40px;
			}
			h1 {
				font-size: 1.8rem;
			}
		}
	}

	@include mobile {
		padding: 15px 0;
		.brand-image {
			height: 40px;
		}
		h1 {
			font-size: 1.5rem;
			padding-left: 20px;
		}
		button {
			display: none;
		}
	}
	
}