@import '../theme.scss';

footer {
	background-color: $bg-primary;
	padding: 15px 0;
	.container {
		display: flex;
		@include mobile {
			display: block;
		}
	}
	.links {
		flex: 1;
		
		a {
			@include mobile {
				width: 100%;
				display: block;
				text-align: center;
				margin: 10px 0;
			}
			color: $grey;
			text-decoration: none;
			font-weight: 500;
			margin-right: 20px;
			font-size: 0.8rem;
			
			&:hover {
				text-decoration: underline;
			}
		}
	}
	.copyright {
		color: $grey;
		font-size: 0.8rem;
		@include mobile { 
			text-align: center;
		}
	}
}